    doRegister: function doRegister() {
      var _this3 = this;

      var params = {
        userName: this.register.userName,
        phone: this.register.phone,
        password: this.register.psd,
        vCode: this.register.code,
        industry: this.industry.id
      };
      if (!this.register.agree) {
        this.register.agreeWarn = '请先接受相关协议';
        return;
      }
      if (!utils["a" /* default */].checkName(this.register.userName) || !this.register.userName) {
        this.registerName.warn = '请输入6到20位之间的用户名';
        this.nameInputWrong = true;
        this.nameInputRight = false;
        this.styleObject.borderColor = '#f56c6c';
        return;
      }
      if (!utils["a" /* default */].checkPhone(this.register.phone)) {
        this.register.warn = '请输入正确的手机号';
        this.phoneInputWrong = true;
        this.phoneInputRight = false;
        this.styleObject2.borderColor = '#f56c6c';
        return;
      }
      if (this.temp === 0 && this.register.codeClick) {
        this.register.vCodeWarn = '请先获取验证码';
        return;
      }
      if (!this.register.code) {
        this.register.vCodeWarn = '请输入验证码';
        return;
      }
      if (!utils["a" /* default */].checkPsw(this.register.psd)) {
        this.register.pswWarn = '请设置6到20位之间的登录密码';
        this.pswInputWrong = true;
        this.pswInputRight = false;
        this.styleObject3.borderColor = '#f56c6c';
        return;
      }

      var loadingInstance = element_ui_common["Loading"].service({
        background: 'rgba(255,255,255,.6)',
        spinner: 'el-icon-loading'
      });
      server["a" /* default */].register(params).then(function (res) {
        if (!res.suc) {
          _this3.register.warn = res.msg;
        } else {
          Object(element_ui_common["Message"])({ type: 'success', message: '注册成功，正在跳转！' });
          server["a" /* default */].login({
            phone: params.phone,
            password: params.password
          }).then(function (res) {
            if (!res.suc) {
              _this3.login.warn = res.msg;
            } else {
              // 登录成功操作。。。
              localStorage.BHTOKEN = res.rs.accessToken;
              location.href = 'manage.html';
            }
          });
        }
      }).catch(function (err) {
        console.log(err);
        Object(element_ui_common["Message"])({ type: 'error', message: '注册失败，请稍后重试!' });
      }).finally(function () {
        loadingInstance.close();
      });
    },


